import React from 'react';
import { Order } from '../models/order';
import { Item } from '../models/item';
import TransportItem from '../TransportItem/TransportItem';
import TransportOrderForm from '../TransportOrderForm/TransportOrderForm';
import './TransportOrderContainer.scss';
import { Button, Form } from "react-bootstrap";

export default class TransportOrderContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            order: Order,
            items: [Item],
            submitFormVisible: false
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    /* Lifecycle Evenets */
    componentDidMount() {
        this.setState({
            order: Order,
            items: [Item],
            submitFormVisible: false
        });
    }

    addItem() {
        this.setState(prevState => ({
            order: prevState.order,
            items: [...prevState.items, Item],
            submitFormVisible: false
        }));
    }
    setItems() {
        this.setState(prevState => ({
            order: prevState.order,
            items: prevState.items,
            submitFormVisible: true
        }));
    }

    handleChange(event, prop) {
        this.setState(prevState => ({
            order: {                   // object that we want to update
                ...prevState.order,    // keep all other key-value pairs
                [prop]: event.target.value       // update the value of specific key
            },
            items: prevState.items,
            submitFormVisible: false
        }));
    }
    handleSubmit(event) {
        console.log('Submit Order:')
        console.log(this.state.order);
        event.preventDefault();
    }

    render() {
        const { items, submitFormVisible } = this.state;
        return (
            <div className="transport-order-container">
                <div className="to-form">
                    <div className='items'>
                        {!submitFormVisible && items.map((item, index) => (
                            <div className="border-box" key={index}>
                                <TransportItem item={item} />
                            </div>
                        ))}
                        {submitFormVisible &&
                            <div className="border-box">
                                <TransportOrderForm order={this.state.order} />
                            </div>
                        }
                    </div>
                    <div className="footer">
                        {!submitFormVisible &&
                            <React.Fragment>
                                <Button variant="primary"
                                    onClick={this.addItem.bind(this)}>
                                    Add
                                </Button>
                                <Button variant="primary"
                                    onClick={this.setItems.bind(this)}>
                                    Done
                                </Button>
                            </React.Fragment>
                        }
                        {submitFormVisible &&
                            <Button variant="primary"
                                onClick={this.addItem.bind(this)}>
                                Submit
                            </Button>
                        }
                    </div>
                </div>
            </div>
        );
    }
}
